// components/dashboard/recovery-button.tsx
"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { AlertCircle } from "lucide-react";

export function RecoveryButton({
  transactionCount,
}: {
  transactionCount: number;
}) {
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<any>(null);

  const handleRecover = async () => {
    setLoading(true);
    try {
      const res = await fetch("/api/recover-my-account", {
        method: "POST",
      });
      const data = await res.json();
      setResult(data);

      if (data.success) {
        alert(
          `✅ Account recovered!\n\nAccount: ${data.account.accountNumber}\nBalance: $${data.account.balance}\nTransactions linked: ${data.transactionsLinked}`
        );
        window.location.reload();
      } else {
        alert(`❌ Recovery failed: ${data.error || data.message}`);
      }
    } catch (error) {
      console.error(error);
      alert("Recovery failed. Check console.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
      <div className="flex items-start gap-3">
        <AlertCircle className="h-5 w-5 text-yellow-600 mt-0.5" />
        <div className="flex-1">
          <h3 className="font-bold text-yellow-900 mb-1">
            Account Recovery Available
          </h3>
          <p className="text-sm text-yellow-800 mb-3">
            We found {transactionCount} transactions but no account. Click below
            to create an account and link your transactions.
          </p>
          <Button
            onClick={handleRecover}
            disabled={loading}
            variant="default"
            size="sm"
            className="bg-blue-600 hover:bg-blue-700"
          >
            {loading ? "Recovering..." : "Recover My Account"}
          </Button>
          {result && (
            <pre className="mt-3 p-2 bg-white rounded text-xs overflow-auto max-h-40 border border-yellow-300">
              {JSON.stringify(result, null, 2)}
            </pre>
          )}
        </div>
      </div>
    </div>
  );
}
